<template>
  <el-col :span="21" class="order-container">
    <div class="form-container">
      <el-col :span="2">
        <el-button type="primary" @click.native.prevent="addGroups">新建分组</el-button>
      </el-col>
    </div>
    <el-table :data="tableData" border style="width: 100%; margin-top: 20px" :max-height="700">
      <el-table-column prop="date" label="组名称" header-align="center" align="center"></el-table-column>
      <el-table-column prop="name" label="佣金比例" header-align="center" align="center"></el-table-column>
      <el-table-column label="操作" header-align="center" align="center">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="addGroups(scope.$index, tableData)"
            type="text"
            size="small"
          >编辑</el-button>
          <el-button
            @click.native.prevent="deleteGroups(scope.$index, tableData)"
            type="text"
            size="small"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="新建/编辑分组" :visible.sync="dialogFormVisible" width="30%">
      <el-input
        v-model="groupName"
        autocomplete="off"
        placeholder="请输入组名称"
        style="margin-bottom: 20px;"
      ></el-input>
      <el-input v-model="commission" autocomplete="off" placeholder="请输入佣金比例，例如0.01"></el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormVisible = false">保 存</el-button>
      </div>
    </el-dialog>
  </el-col>
</template>

<script>
export default {
  name: "Order",
  data() {
    return {
      dialogFormVisible: false,
      groupName: "",
      commission: "",
      tableData: [
        {
          date: "2016-05-03",
          name: "王小虎"
        }
      ]
    };
  },
  created() {},
  watch: {},
  methods: {
    addGroups(index, tableData) {
      console.log("params:", index, tableData);
      this.dialogFormVisible = true;
    },
    deleteGroups(scope, index, tableData) {
      const num = Math.random() * 10;
      if (num > 5) {
        this.$message.error("有训练师属于该分组，无法删除");
        return;
      }
      this.$confirm("确定删除分组吗?", "提示", {
        confirmButtonText: "删除",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  }
};
</script>
